import React,{Fragment, useState, useEffect} from 'react';
import { useParams } from "react-router-dom";

 
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import axios from 'axios'

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  large: {
    width: theme.spacing(20),
    height: theme.spacing(20),
  },
}));

export default function PokeInfo(){
	const classes = useStyles();
	const {id} = useParams();
	const [pokemon, setPokemon] = useState([]); //puede llamarse con React.useState, sin enl import {...}

	React.useEffect(() =>{
		 obtenerDatos();
	}, [])

	const obtenerDatos = async () => {
		axios.get(`https://pokeapi.co/api/v2/pokemon/${id}`).then(res =>{
			console.log(res.data.sprites);
			setPokemon(res.data.sprites);
		}).catch((error) => {
            console.log(error)
        })
 
	}

	return (
		<Fragment>
		<Container  maxWidth="md">
		  <Avatar alt="Remy Sharp" src={pokemon.front_default} className={classes.large} />
		 

		</Container>
		</Fragment>
	)
}